html, body, #root {
  min-height: 100%;
}

a{
  text-decoration: none;
  color: black;
}

/* html, body {
  min-height: 100vh;
} */


.App{min-height:100vh}


.vending-machine{background-color:#DCDCDC;border-radius:4%;min-width:25vw;height:80vh;background-color:#D0D0D0;margin-left:3rem;overflow:hidden;box-shadow:0px 0px 5px}@media (max-width: 910px){.vending-machine{margin:5px 0;width:62.5vw}}

.display{height:15%;display:flex;justify-content:center;align-items:center;background-color:#D0D0D0}.display .display-text{width:75%;display:flex;justify-content:center;align-items:center;border-radius:25px;height:50%;background-color:black;font-family:'Orbitron', sans-serif;font-size:small;font-weight:bold;color:#33cc33;overflow:hidden;text-overflow:ellipsis;text-align:center}

.image{width:90%;height:70%;display:flex;justify-content:center;align-items:center;margin:0 auto;background-image:url(/react-vending-machine/static/media/VendingMachine1.054a408d.png);background-position:center;background-repeat:no-repeat;background-size:cover}

.cost-buttons{display:flex;justify-content:space-evenly;align-items:center;height:15%}

.product-info-container{border-radius:20px;height:75vh;min-width:62.5vw;margin-right:3rem;background-color:#D3D3D3;font-family:'Poppins', sans-serif;display:flex;flex-direction:column;justify-content:center}@media (max-width: 910px){.product-info-container{margin:0}}

.product-info{display:flex;justify-content:center;align-items:center;height:30%}.product-info .product-info-card{width:45%;height:95%;border:2px solid gray;border-radius:25px;overflow:hidden}.product-info .product-info-card .product-name{height:50%;display:flex;justify-content:center;align-items:center;font-size:2.5vw}.product-info .product-info-card .nutrition-info{height:50%;width:100%;display:flex;justify-content:flex-start}.product-info .product-info-card .nutrition-info .protein{width:20%;display:flex;flex-direction:column;align-items:center;font-size:1.45vw;justify-content:center}.product-info .product-info-card .nutrition-info .facts{width:20%;display:flex;flex-direction:column;border-right:2px solid grey;align-items:center;justify-content:center;font-size:1.43vw}.product-info .product-info-card .nutrition-info p{text-decoration:underline}

.drink-list-container{display:flex;justify-content:space-evenly;align-items:center;height:30%;overflow:hidden}.drink-list-container .drink-card{display:flex;flex-direction:column;justify-content:space-between;height:95%;width:20%;border-radius:25px;background-color:white;background-position:center;background-repeat:no-repeat;background-size:contain;box-shadow:0px 0px 5px}.drink-list-container .drink-card .info-buttons{width:100%;height:30%;display:flex;justify-content:space-between}.drink-list-container .drink-card .info-buttons .drink-stock{display:flex;justify-content:center;align-items:center;width:25%;height:100%;border-radius:20px;margin-top:0.25rem;margin-right:0.5rem;font-weight:bold}.drink-list-container .drink-card .product-name-container{display:flex;justify-content:center;background-color:lightgray}.drink-list-container .drink-card .product-name-container .product-name{color:#434446;font-weight:bold;text-align:center}.drink-card:hover{border:1px solid black}.drink-card:hover .buy-button{display:block !important}

.snack-list-container{display:flex;justify-content:space-evenly;align-items:center;height:30%;overflow:hidden}.snack-list-container .snack-card{display:flex;flex-direction:column;justify-content:space-between;height:95%;width:20%;border-radius:25px;background-color:white;background-position:center;background-repeat:no-repeat;background-size:contain;box-shadow:0px 0px 5px}.snack-list-container .snack-card .snack-info-buttons{width:100%;height:30%;display:flex;justify-content:space-between}.snack-list-container .snack-card .snack-info-buttons .snack-stock{display:flex;justify-content:center;align-items:center;width:25%;height:100%;border-radius:20px;margin-top:0.25rem;margin-right:0.5rem;font-weight:bold}.snack-list-container .snack-card .snack-name-container{display:flex;justify-content:center;background-color:lightgray}.snack-list-container .snack-card .snack-name-container .snack-name{color:#434446;font-weight:bold;text-overflow:ellipsis;text-align:center}.snack-card:hover{border:1px solid black}.snack-card:hover .buy-button{display:block !important}

.vending-info{display:flex;justify-content:space-between;align-items:center;background-color:#F8F8F8;box-shadow:0px 0px 2.5px}@media (max-width: 910px){.vending-info{display:flex;flex-direction:column;align-items:center}}

.product-list{width:85vw;height:100vw;margin:1rem auto;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:20px}.product-list .refill-card{display:flex;flex-direction:column;justify-content:space-between;border-radius:25px;height:300px;background-position:center;background-repeat:no-repeat;background-size:contain}.product-list .refill-card .info-buttons{height:15%;width:100%;display:flex;justify-content:center}.product-list .refill-card .info-buttons .drink-stock{height:100%;width:2.5rem;border-radius:50%;display:flex;justify-content:center;align-items:center}.product-list .refill-card .product-name-container{background-color:grey;height:15%;border-top-left-radius:25px;border-top-right-radius:25px;font-weight:bold}

.product-card{display:flex;flex-direction:column;background-color:#95dada;border:1px solid grey;border-radius:5px;padding:25px;cursor:pointer;-moz-osx-font-smoothing:grayscale;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:translateZ(0);transition:transform 0.25s ease-out;font-weight:bold}

